import style from './SushiItem.module.css';


const SushiItem = (props) => {
    return (
        <div className={style.item}>
            <div>: {props.name}</div>
            <div>: {props.price}</div>
            <div>: {props.description}</div>
            <div className={style.ItemImg}>: {<img src={`${props.imgUrl}`} ></img>}</div>
        </div>
    )
}
export default SushiItem;